@import url(https://fonts.googleapis.com/css?family=Caveat&display=swap);
* {
  padding: 0;
  margin: 0;
}

body {
  overflow-x: hidden;
}

.menu {
  padding: 0 20px;
  border-bottom: solid 1px #e8e8e8;
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1;
  background-color: white;
}

.menu__logo {
  width: 150px;
  float: left;
}

.menu__logo a {
  display: inline-block;
  font-size: 34px;
  font-weight: 700;
  padding: 19px 20px;
  letter-spacing: 0.2em;
  color: #000;
  font-family: "Caveat", cursive;
}

.menu__logo a:hover {
  color: #fe2851;
}

@media (max-width: 767px) {
  .menu__logo a {
    margin-left: -20px;
    padding: 10px 20px;
  }
}

col div {
  position: relative;
}

img {
  width: 100%;
  height: 320px;
  transition: all 1s ease-out;
}

.poke-card:hover {
  background-color: lightskyblue;
}

